body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.header {
  height: 50px;
}

.header-buttons {
  display: flex;
  justify-content: flex-end;
}

.button, .send-form {
  margin: 5px;
}

.modal-form {
  padding: 20px;
}

.title-form {
  margin-top: 20px;
}

.avatar {
  height: 50px;
  width: 50px;
  transition: 0.1s;
}

.avatar:hover {
  transform: rotate(10deg);
}

.main {
  padding: 40px 0;
}

.spinner-block {
  display: flex;
  justify-content: center;
}

.spinner {
  margin-top: 25%;
}

.message-row {
  color: #343a40;
  position: relative;
}

@media screen and (max-width: 496px) {
  .message-row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .block-message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.my {
  background-color: #f3f3f3;
}

.block-message-avatar {
  display: flex;
  justify-content: center;
  min-width: 80px;
}

.message-avatar {
  width: 80px;
  height: 80px;
  margin: 5px 5px 5px 20px;
}

.message-nickName {
  word-wrap: break-word;
  font-size: 24px;
  margin: 5px;
}

.message-text {
  word-wrap: break-word;
  font-size: 20px;
  padding-bottom: 5px;
  margin: 0 10px 0 0;
}

.message-date {
  font-size: 10px;
  padding-bottom: 10px;
  margin: 0;
}

.footer {
  height: 50px;
}